<template>
  <div class="hello">
    <h1>{{ msg }}</h1>
    <b-button variant="primary" v-on:click="paixu()">随机选人</b-button>
    <br>

   <b-container>
    <b-row>
    <b-col>
    <label for="tags-basic">蜀国出场人员</label>
    <b-container style="width:300px">
      <b-form-tags input-id="tags-basic" v-model="sg1" ></b-form-tags>
      <b-form-select v-model="selectsg" :options="sg3" size="sm" class="mt-3"></b-form-select>
    </b-container>
    <!-- <p class="mt-2">Value: {{ value }}</p> -->

    <b-container class="bv-example-row" style="width:210px">
      
      <b-row v-for="(item,i) in sg1" :key=i> 
        <b-col>{{item}}</b-col>
        <b-col>-</b-col>
        <b-col>{{sg2[i]}}</b-col>
      </b-row> 
    </b-container>
    </b-col>
    <b-col>
    <label for="tags-basic">魏国出场人员</label>
    <b-container style="width:300px">
      <b-form-tags input-id="tags-basic" v-model="wg1" ></b-form-tags>
      <b-form-select v-model="selectwg" :options="wg3" size="sm" class="mt-3"></b-form-select>
    </b-container>
    <!-- <p class="mt-2">Value: {{ value }}</p> -->

    <b-container class="bv-example-row" style="width:210px">
      
      <b-row v-for="(item,i) in wg1" :key=i> 
        <b-col>{{item}}</b-col>
        <b-col>-</b-col>
        <b-col>{{wg2[i]}}</b-col>
      </b-row>
      
    </b-container>
</b-col>
</b-row>
  </b-container>
  </div>
</template>

<script>
import _ from 'lodash';

export default {
  name: 'HelloWorld',
  props: {
    msg: String
  },
  data: function(){
      return {
        text:'',
        sg1: ['博', '叛','天', '潇','波'],
        sg2:['赵云','诸葛','关羽','张飞','魏延'],
        wg1:['莫','梦','风','诺','沙'],
        wg2:['司马','郭嘉','荀彧','典韦','曹仁'],
        selectsg:null,
        selectwg:null
      }
  },
  computed: {
    sg3: function () {
      return [{ value: null, text: '定尾发' },...this.sg1]
    },
    wg3: function () {
      return [{ value: null, text: '定尾发' },...this.wg1]
    }
  },
  methods: {
    paixu: function(){

      if (this.selectsg != null) {
         this.sg1.splice(this.sg1.indexOf(this.selectsg),1)
         this.sg1.push(this.selectsg)
      }

      if (this.selectwg != null) {
         this.wg1.splice(this.wg1.indexOf(this.selectwg),1)
         this.wg1.push(this.selectwg)
      }

      for (var i=1;i<100;i++) {
      setTimeout(()=>{
        this.sg2 = _.shuffle(this.sg2);
        this.wg2 = _.shuffle(this.wg2);
        },100);
      }
      
      setTimeout(()=>{
            if (this.selectsg != null) {
              this.sg2.splice(this.sg2.indexOf('魏延'),1)
              this.sg2.push('魏延')
            }

            if (this.selectwg != null) {
              this.wg2.splice(this.wg2.indexOf('曹仁'),1)
              this.wg2.push('曹仁')
            }
      },1000);
    
    }
    }
}

</script>

